* {
    box-sizing: border-box;
}

:root {
    --color-1st: #202225;
    --color-2nd: #36393f;
    --color-3rd: #2f3136;
    --color-4th: rgba(79, 84, 92, 0.32);
    --color-5th: #40444b;
    --color-6th: #fff;
    --color-7th: #dcddde;
    --color-8th: #b9bbb3;
    --color-9th: #8e9297;
    --color-10th: #18191c;
    --color-11th: #b9bbbe;
    --color-12th: hsl(235, 46.7%, 44.1%);
    --color-13th: #292b2f;
    --color-14th: rgba(79, 84, 92, 0.16);
    --color-16th: #2b2e33;
    --color-17th: #5865f2;
    --color-18th: hsl(139, 47.3%, 43.9%);
    --color-19th: hsl(359, 82.6%, 59.4%);
    --color-20th: hsl(235, 51.4%, 52.4%);
    --color-21th: #e0e3e5;
    --color-22th: rgba(0, 0, 0, 0.85);
    --color-23th: hsl(235, 86.1%, 77.5%);
    --color-24th: #303339;
    --color-25th: rgba(0, 0, 0, 0.3);
    --color-26th: #040405;
    --color-27th: hsl(197, 100%, 47.8%);
    --color-28th: #72767d;
    --color-29th: hsl(235, 85.6%, 64.6%);
    --color-30th: hsl(235, 51.4%, 52.4%);
    --color-31th: rgba(114, 118, 125, 0.3);
    --color-32th: hsla(0, 0%, 100%, 0.06);
    --color-33th: hsl(210, 2.9%, 86.7%);
    --color-34th: #4f545c;
    --small-shadow-bar: 0 1px 0 rgba(4, 4, 5, 0.2), 0 2px 0 rgba(6, 6, 7, 0.05), 0 2px 0 rgba(4, 4, 5, 0.05);
    --font-1st: Whitney, "Helvetica Neue", Helvetica, Arial, sans-serif;
    --font-2nd: Ginto, "Helvetica Neue", Helvetica, Arial, sans-serif;
}

body {
    margin: 0;
    padding: 0;
    position: absolute;
    height: 100%;
    width: 100%;
    font-family: var(--font-1st);
}

#root {
    height: 100%;
    width: 100%;
    display: grid;
    grid-template: 48px 1fr / 72px 240px 1fr 240px;
    grid-template-areas:
                "first-panel second-panel header header"
    "first-panel second-panel third-panel forth-panel";
}

ul, ol {
    list-style: none;
    margin: 0;
    padding: 0;
}

button {
    cursor: pointer;
    background: none;
    border: none;
    color: inherit;
}

.red-line {
    color: var(--color-19th);
}

.panel {
    display: flex;
    flex-direction: column;
}

.panel ul {
    overflow-y: auto;
    flex-grow: 1;
}

.first-panel {
    grid-area: first-panel;
    overflow-y: auto;
    background-color: var(--color-1st);
    display: flex;
    align-items: center;
    padding-top: 20px;
}

.first-panel button {
    background-color: var(--color-2nd);
    border: thin solid var(--color-2nd);

    color: white;
    font-size: 24px;
    border-radius: 50%;
    width: 48px;
    height: 48px;
    transition: background-color 150ms, border-radius 150ms;
}

.first-panel button:hover, .first-panel-button-selected {
    background-color: var(--color-17th) !important;
    border-radius: 30% !important;
}

.second-panel {
    grid-area: second-panel;
    position: relative;
}

.second-panel header {
    height: 48px;
    flex-shrink: 0;
    background-color: var(--color-3rd);
    color: var(--color-6th);
    box-shadow: var(--small-shadow-bar);
    z-index: 0;
}

.second-panel header > * {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
}

.second-panel header span {
    cursor: default;
}

.second-panel .dropdown {
    position: absolute;
    top: 48px;
}

.second-panel ul {
    background-color: var(--color-3rd);
    color: var(--color-9th);
}

.second-panel ul button {
    width: 100%;
    text-align: start;
}

.second-panel ul button:hover {
    color: var(--color-7th);
}

.second-panel footer {
    background-color: var(--color-13th);
    color: var(--color-11th);
    display: flex;
    height: 52px;
    justify-content: center;
    align-items: center;
    padding: 0 8px;
}

.second-panel footer button {
    padding: 6px;
    border: 0;
    border-radius: 4px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.second-panel footer button:hover {
    color: var(--color-7th);
    background-color: var(--color-4th);
}

.second-panel footer span {
    flex-grow: 1;
    cursor: default;
    color: var(--color-6th);
}

.header {
    grid-area: header;
    background-color: var(--color-2nd);
    color: var(--color-11th);
    display: flex;
    align-items: center;
    box-shadow: var(--small-shadow-bar);
}

.header span {
    /*flex-grow: 1;*/
    color: var(--color-6th);
    cursor: default;
}

.header .channel-name {
    flex-grow: 1;
}

.header input {
    width: 200px;
    margin: 0 20px;
}

.header button:hover {
    color: var(--color-7th);
}

.third-panel {
    grid-area: third-panel;
    overflow-y: hidden;
    background-color: var(--color-2nd);
}

.third-panel footer {
    grid-area: third-panel;
    overflow-y: hidden;
    background-color: var(--color-5th);
    display: flex;
    align-items: start;
    margin: 0 16px 24px;
    border: 0;
    border-radius: 8px;
    min-height: 44px;
    max-height: 220px;
    color: var(--color-11th);
}

.third-panel footer button:first-child {
    padding: 10px 16px;
}

.third-panel footer button:not(:first-child) {
    margin: 8px;
}

.third-panel footer button:hover {
    color: var(--color-7th);
}

.third-panel footer button div {
    background-position: 0 0;
    background-size: 242px 110px;
    background-repeat: no-repeat;
    width: 22px;
    height: 22px;
    transform: scale(1);
    filter: grayscale(100%);
}

.third-panel footer button div:hover {
    transform: scale(1.14);
    filter: grayscale(0%);
}

.third-panel footer span {
    flex-grow: 1;
}

.forth-panel {
    grid-area: forth-panel;
    overflow-y: auto;
    background-color: var(--color-3rd);
    color: var(--color-9th);
    cursor: default;
}

.forth-panel li {
    display: flex;
    align-items: center;
    padding: 10px;
}